import React from 'react';
import './Rst.css';

class Rst extends React.Component {
  render() {

    let classes = 'rst'
                + ' rst-' + this.props.type
                + (this.props.i % 2 ? ' rst-right'
                                    : ' rst-left');

    return (
      <div className={classes}>
        <div style={{position: "relative"}}>
          <div className="rst-dot">&#9632;</div>
          <div className="rst-label">{ this.props.label }</div>
        </div>
      </div>
    );
  }
}

export default Rst;
