import React from 'react';
import './PortMenu.css';

var options = ["NEW",
               "U-144",
               "E-344",
               "U-093"]

class PortMenu extends React.Component {
  render() {
    return (
      <div className="port-menu">
        <select value={ this.props.selected }>
          { options.map(o => <option>{o}</option> ) }
        </select>
      </div>
    );
  }
}

export default PortMenu;
