import React from 'react';
import './Activities.css';

class DayGroup extends React.Component {
  render() {
    return (
      <div>
        <div className="activity-date">{this.props.date}</div>

        { this.props.records.map( r =>
            <div className="activity-record">{r}</div> ) }

      </div>
    );
  }
}

class Activities extends React.Component {
  render() {
    
    let days = this.props.activities.map(day =>
                 <DayGroup date={day.date}
                           records={day.records} />);

    return (
      <div>
        {days}
      </div>
    );
  }
}

export default Activities;
