import React from 'react';
import './ResOrb.css';

class ResOrb extends React.Component {
  render() {
    
    // if it's not a res then it's an orb
    let type = this.props.type === "res" ? "RES" : "ORB";

    let orb = <div className="orb-name">{type}-{this.props.id}</div>;
    let bar = <div className="orb-bar">
                <span className="orb-range">0-{this.props.max}</span>
                <span className="orb-a">A{this.props.a}</span>
              </div>;

    return (
      <div className="res-orb">
        {orb}
        {bar}
      </div>
    );
  }
}

export default ResOrb;
